<template>
  <div>
    <bread-nav :nav="nav"/>
    <main-part>
      <div class="btn-block">
        <span class="btn-main-deepen"
              @click="goAdd">发布</span>
      </div>
      <search-box :searchData="searchData" :table="table"/>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="25%">标题</th>
          <th width="10%">发布时间</th>
          <th width="25%">范围</th>
          <th width="10%">参与人数</th>
          <th width="10%">截止时间</th>
          <th width="10%">状态</th>
          <th width="10%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, idx) in table.list"
            :key="idx">
          <el-tooltip :content="item.title" placement="top" effect="dark">
            <td class="text-overflow">{{item.title}}</td>
          </el-tooltip>
          <td>{{item.createdTime | substrTime}}</td>
          <td v-if="item.scopeList">{{item.scopeList.map(scope => scope.scopeName).join(', ')}}</td>
          <td>{{item.participatedNum}}</td>
          <td>{{item.endTime | substrTime}}</td>
          <td>{{item.isFinish === 'YES' ? '已结束' : '进行中'}}</td>
          <td>
            <span class="oper"
                  @click="goDetail(item.id)">查看</span>
            <span class="oper"
                  @click="doDelete(item.id)">删除</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"/>
      <paging :data="table"/>
    </main-part>

  </div>
</template>

<script>
  export default {
    data () {
      return {
        nav: [{name: '调查问卷', path: ''}],
        searchData: [
          {type: 'input', placeholder: '标题'}
        ],
        table: {
          url: '/survey/querySurveyPage.do',
          el: '.table',
          noData: true,
          query: {
            keywords: null,
            pageNo: 1,
            pageSize: 10
          },
          list: [],
          total: 0
        }
      }
    },
    mounted () {
      this.$search(this.table)
    },
    methods: {
      goAdd () {
        this.$router.push('/student-work/survey/add')
      },
      goDetail (id) {
        this.$router.push('/student-work/survey/' + id)
      },
      async doDelete (id) {
        await this.$confirm('确认删除该调查问卷? 删除后无法恢复', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        try {
          await this.$ajax({
            url: '/survey/deleteSurvey.do',
            query: {
              id
            }
          })
          this.$remind('删除')
          this.$search(this.table)
        } catch (e) {}
      }
    }
  }
</script>

